import React from 'react';
// import http from 'http';
import './App.css';
import Historical from './historical';
import { ResponsiveContainer, LineChart, Line, XAxis, YAxis, Tooltip, Legend } from 'recharts';

class App extends React.Component{
  state = {
    loading: true,
    data: []
  }

  componentDidMount(){
    console.log("mounted")

    this.fetchData();
    //TODO:
    //load current and future
    //limit future to rafting day
    //vertical line current/future
    //different line style for future. dashes?
    //load historical data for all values current


    // this.setState({
    //   loading: false, 
    //   data: [
    //     { name: 'date', 2018: 4000, 2019: 2400 },
    //     { name: 'date', 2018: 3000, 2019: 1398 },
    //     { name: 'date', 2018: 2000, 2019: 9800 },
    //     { name: 'date', 2018: 2780, 2019: 3908 },
    //     { name: 'date', 2018: 1890, 2019: 4800 },
    //     { name: 'date', 2018: 2390, 2019: 3800 },
    //     { name: 'date', 2018: 3490, 2019: 4300 }
    //   ]
    // })
  }

  render(){
    return <div className="App">
        <h1>Pemi River Height</h1>
        <ResponsiveContainer width="100%" height={500}>
          <LineChart
            data={this.state.data}
            margin={{ top: 5, right: 30, left: 20, bottom: 5 }}
          >
            {/* <CartesianGrid /> */}
            <XAxis dataKey="name" hide={true} />
            <YAxis hide={false} />
            <Tooltip />
            <Legend />
            <Line type="monotone" dataKey="2014" stroke="#82ca9d" />
            <Line type="monotone" dataKey="2015" stroke="#82ca9d" />
            <Line type="monotone" dataKey="2016" stroke="#82ca9d" />
            <Line type="monotone" dataKey="2017" stroke="#82ca9d" />
            <Line type="monotone" dataKey="2018" stroke="#82ca9d" />
            <Line type="monotone" dataKey="2019" strokeWidth="5" stroke="#8884d8" activeDot={{ r: 8 }} />
          </LineChart>
        </ResponsiveContainer>
    </div>
  }

  fetchData(){
    const url = "https://water.weather.gov/ahps2/hydrograph_to_xml.php?gage=woon3&output=xml";
    const data = [];

    fetch(url)
      .then(response => response.text())
      .then((response) => { 
        
        const parser = new DOMParser();
        const xml = parser.parseFromString(response, 'text/xml');
        
        const observed = xml.querySelectorAll('observed > datum');
        let latestTime : string = '';
        observed.forEach(function(item){
          let datetime = new Date(item.querySelector('valid').innerHTML).toLocaleString().trim()
          let date = datetime.split(', ')[0];
          let month = date.split('/')[0]
          let day = date.split('/')[1]
          let time = datetime.split(', ')[1];
          if(time.includes('00:00')){
            if(!latestTime){ latestTime = time.split(' ')[0]; } //get time without am or pm so we get every 12th hour
            let ft = item.querySelector('primary').innerHTML


            if(time.split(' ')[0] === latestTime){  

              data.unshift({
                name: month + '/' + day + ' ' + time, 
                2019: ft,
                2018: Historical.filter(function(obs){return obs.Date === month + '/' + day + '/' + 2018 + ' ' + time})[0].Observed,
                2017: Historical.filter(function(obs){return obs.Date === month + '/' + day + '/' + 2017 + ' ' + time})[0].Observed,
                2016: Historical.filter(function(obs){return obs.Date === month + '/' + day + '/' + 2016 + ' ' + time})[0].Observed,
                2015: Historical.filter(function(obs){return obs.Date === month + '/' + day + '/' + 2015 + ' ' + time})[0].Observed,
                2014: Historical.filter(function(obs){return obs.Date === month + '/' + day + '/' + 2014 + ' ' + time})[0].Observed,
              })
            }
          }
        })

        // const forecast = xml.querySelectorAll('forecast > datum');
        // forecast.forEach(function(item){
        //   var d = new Date(item.querySelector('valid').innerHTML).toLocaleString().replace(',','');
        //   var ft = item.querySelector('primary').innerHTML

        //   console.log(d)
        // })

        //console.log(observed);
        // console.log(forecast);

        this.setState({
          loading: false, 
          data: data
        })
      });
  }
}

export default App;
